/** @jsx jsx */
import React from "react"
import { jsx, Link as TLink } from "theme-ui"
import { Box } from "@theme-ui/components"
import { Link } from "gatsby"
import { Author } from "../../../gatsby-plugin-theme-ui/components"
import Gravatar from 'react-gravatar'
import ItemTags from "@lekoarts/gatsby-theme-minimal-blog/src/components/item-tags"

type BlogListItemProps = {
  post: {
    frontmatter: {
      [key: string]: any
    }
    slug: string
    title: string
    date: string
    excerpt: string
    description: string
    timeToRead?: number
    tags?: {
      name: string
      slug: string
    }[]
  }
  showTags?: boolean
}

const BlogListItem = ({ post, showTags = true }: BlogListItemProps) => (
  <Box mb={"44px"}>
    <TLink as={Link} to={post.slug} style={{ fontWeight: "bolder" }} sx={{ fontSize: [3, "1.7em", "1.7em"], color: `text` }}>
      {post.title}
    </TLink>
    <p style={{ margin: "0", fontSize: "17px" }}>{post.description}</p>
    
    <div style={{display: "flex", alignItems:'center'}} sx={{ color: `secondary`, mt: 1, a: { color: `secondary` }, fontSize: [1, 1, 1] }}>
      {post.frontmatter
        && post.frontmatter.author
        && post.frontmatter.email &&
      
        <span style={{display:"flex", alignItems:"center", marginRight: 10}}>
          <Gravatar size={35} email={post.frontmatter.email} style={{ borderRadius: "25px", marginRight: 10 }} />
          { post.frontmatter.author }
        </span>
      }
      <time>{post.date}</time>    
    </div>
  </Box>
)

export default BlogListItem
